@tailwind base;
@tailwind components;
@tailwind utilities;

  @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&family=Rubik:wght@400;500;700&display=swap');

  :root{
    --color-yellow:#f9c74f;
    --color-purple:#9d4eed;
  }
*>*{
  font-family: 'Poppins', sans-serif;
margin:0;padding:0;
}
.App {
  text-align: center;
  padding:4em 0;
}

.bg-yellow{
  background-color:var(--color-purple) ;
}
.chart .title{
  position: absolute;
  left:0;
  right:0;
  top:40%;
  margin-left: auto;
  margin-right: auto;
}
.form-input{
  @apply mt-1 block w-full py-2 px-3 border border-gray-200 bg-white rounded-md focus:outline-none focus:ring-indigo-500 sm:text-sm
}